.shopcategory-banner{
    display: block;
    margin:3px auto;
    width:82%;
}

.shopcategory-indexsort{
    display:flex;
    margin: 15px 170px;
    justify-content: space-between;
    align-items: center;
}

.shopcategory-sort{
    padding:10px 20px;
    border-radius: 40px;
    border: 1px solid #888;
}
.shopcategory-indexsort p sapn{
    font-weight: 600;
}

.shopcategory-products{
    margin:20px 70px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 80px;
    gap:15px;
}

.shopcategory-loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin:70px auto;
    width:233px;
    height:69px;
    border-radius: 75px;
    background: #ededed;
    color:#787878;
    font-size: 18px;
    font-weight: 500;
}