.offers{
    width:85%;
    height:65vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 100px;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
    /* gap:10px; */
}

.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1{
    color:#171717;
    font-size: 38px;
    font-weight: 600;
}

.offers-left p{
    color:#171717;
    font-size: 13px;
    font-weight: 600;
}

.offers-left button{
    width:170px;
    height:43px;
    border-radius: 35px;
    border: none;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top:50px ;
    margin-left: 80px;
}