.newsletter{
    width:85%;
    height:45vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    gap:10px;
    background:linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
}

.newsletter h1{
    color:#454545;
    font-size: 45px;
    font-weight: 600;
}

.newsletter p{
    color: #454545;
    font-size: 20px;
}

.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background:white;
    width:730px;
    height:55px;
    border-radius: 80px;
    border:1px solid #e3e3e3;
}

.newsletter input{
    width:500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color:#616161;
    font-family: Poppins;
    font-size: 16px;
}

.newsletter div button{
    width:210px;
    height:55px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}