.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:10px;
    height:190vh;
}

.new-collections h1{
    color: #171717;
    font-size: 45px;
    font-weight: 500;
}

.new-collections hr{
    width:200px;
    height:6px;
    border-radius: 10px;
    background: #252525;
}

.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap:30px;
}